.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-size: 50px;
  text-shadow: 2px 2px 5px red;
}

.highlighted .my_target {
  background-color: 'lightblue';
}

.my_target {
  margin-top: 300px;
  margin-right: 400px;
}

.counter {
  width: 100%;
  text-align: right;
  font-size: 40px;
  text-shadow: 2px 2px 5px red;
}

.game {
  margin-top: 300px;
}
